import { Container, createTheme, makeStyles, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, ThemeProvider, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom';
import { CryptoState } from '../../context/CryptoContext';
import { numberWithCommas } from '../Carousel/Carousel';
import { Pagination } from '@material-ui/lab';

const CoinsTable = () => {
  const [search, setSearch] = useState("");
  const history = useHistory();
  const {currency, symbol, coins, loading, fetchCoins} = CryptoState();
  const [page, setPage] = useState(1);

  useEffect(() => {
    fetchCoins();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currency]);

  const darkTheme = createTheme({
    palette:{
      primary:{
        main: "#ffff"
      },
      type: "dark",
    }
  })

  const handleSearch = () => {
    return coins.filter((coin)=>
      coin.name.toLowerCase().includes(search) || coin.symbol.toLowerCase().includes(search)
    )
  }

  const useStyles = makeStyles(() => ({
    row:{
      backgroundColor: "#16171a",
      cursor: "pointer",
      "&:hover":{
        backgroundColor: "#131111",
      },
      fontFamily: "Open Sans"
    },
    pagination:{
      "& .MuiPaginationItem-root":{
        color: "gold",
      }
    }
  }));
  const classes = useStyles();

  
  return (
    <ThemeProvider theme={darkTheme}>
      <Container style={{textAlign: "center"}}>
        <Typography variant="h4" style={{margin: 18, fontFamily: "Open Sans"}}>
        Precios de las Criptodivisas
        </Typography>
        <TextField label="Busqueda de Criptomoneda"
        style={{
          marginBottom: 20,
          width: "100%"
        }}
        onChange={(e)=>setSearch(e.target.value)}/>
        <TableContainer>
          { 
            <Table>
              <TableHead style={{backgroundColor: "#EEBC1D"}}>
                <TableRow>
                {["Divisa", "Precio", "Cambios en las Ãºltimas 24 hrs", "Valor de mercado"].map((head) => (
                  <TableCell 
                  style={{
                    color: "black",
                    fontWeight: "700",
                    fontFamily: "Open Sans",
                  }}
                  key={head}
                  align={head === "Divisa" ? "justify" : "center"}>
                    {head}
                  </TableCell>
                ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {handleSearch().slice((page-1)*10, (page-1)*10+10).map(row => {
                  const profit = row.price_change_percentage_24h > 0;
                  return(
                    <TableRow onClick={() => history.push(`/coins/${row.id}`)}
                    className={classes.row}
                    key={row.name}>
                      <TableCell component="th" scope="row"
                      style={{
                        display: "flex",
                        gap: 15,
                      }}>
                        <img src={row?.image}
                        alt={row.name}
                        height="50"
                        style={{
                          marginBottom: 10,
                        }} />
                        <div style={{
                          display: "flex",
                          flexDirection: "column"
                        }}>
                          <span style={{
                            textTransform: "uppercase",
                            fontSize: 22,
                          }}>
                            {row.symbol}
                          </span>
                          <span style={{ color: "darkgrey"}}>
                            {row.name}
                          </span>
                        </div>
                      </TableCell>
                      <TableCell align="center">
                        {symbol}{" "}
                        {numberWithCommas(row.current_price.toFixed(2))}
                      </TableCell>
                      <TableCell align="center"
                      style={{
                        color: profit > 0 ? "rgb(14,203,129" : "red",
                        fontWeight: 500,
                      }}>
                        {profit && " "}
                        {row.price_change_percentage_24h.toFixed(2)}%
                        </TableCell>
                        <TableCell align="center">
                          {symbol}{" "}
                          {numberWithCommas(row.market_cap.toString().slice(0, -6))}MM
                        </TableCell>
                    </TableRow>
                  )
                })}
              </TableBody>
            </Table>
          }
        </TableContainer>
        <Pagination 
        style={{
          padding: 20,
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
        classes = {{ ul: classes.pagination}}
        count = {(handleSearch()?.length/10).toFixed(0)} 
        onChange={(_, value) => {
          setPage(value);
          window.scroll(0, 450);
        }}/>
      </Container>
    </ThemeProvider>
  )
}

export default CoinsTable